.header {
  position: relative;
  top: 0;
  padding-top: 20px;
  padding-bottom: 20px;

  @media (width <=787px) {
    padding-bottom: 0;
  }

  &__nav {
    align-items: center;
    justify-content: space-between;
    width: 100%;

    @media (width <=787px) {
      flex-direction: column;
    }
  }

  &__link {
    border-radius: 8px;
    padding: 10.5px 30px;
    font-size: 14px;
    text-transform: uppercase;
    color: var(--text-color);
    background-color: var(--button-bg-color);

    &:focus {
      outline: none;
    }

    @media (width <=787px) {
      align-self: flex-start;
      margin-bottom: 10px;
    }
  }

  &__link-logo {
    border-radius: 0;
    padding: 0;
    font-family: var(--second-family);
    font-weight: 900;
    font-size: 32px;
    color: var(--subaccent-color);
    background-color: transparent;
  }

  &__item {
    &:not(:last-child) {
      margin-right: 20px;
    }
  }

  &__wrapper {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    max-width: 470px;
    gap: 10px;

    @media (width <=787px) {
      align-self: flex-end;
    }

    @media (width <=480px) {
      flex-flow: wrap;
      flex-direction: row-reverse;
      max-width: 100%;

      .header__container {
        order: 1;
        width: 100%;
      }
    }
  }

  &__btn {
    outline: none;
    transition: all .3s ease-in-out;
  }

  &__btn-theme {
    display: flex;
    align-items: center;
    border-radius: 8px;
    padding: 9px 10px;
    background-color: var(--button-bg-color);

    @include hover {
      background-color: var(--text-color);

      .header__icon {
        fill: var(--bg-color);
        transition: all .3s ease-in-out;
      }
    }

    &:focus {
      background-color: var(--text-color);

      .header__icon {
        fill: var(--bg-color);
      }
    }
  }

  &__btn-contacts {
    border-radius: 8px;
    padding: 11px 30px;
    font-size: 14px;
    text-transform: uppercase;
    color: var(--text-color);
    background-color: var(--button-bg-color);

    @include hover {
      color: var(--bg-color);
      background-color: var(--subaccent-color);
    }

    &:focus {
      outline: none;
      color: var(--bg-color);
      background-color: var(--subaccent-color);
    }
  }

  &__icon {
    width: 20px;
    height: 20px;
    fill: var(--text-color);
  }

  &__search {
    outline: none;
    border: 1px solid var(--button-bg-color);
    border-radius: 8px;
    padding: 10px 15px;
    min-width: 242px;
    font-size: 14px;
    color: var(--text-color);
    background-color: var(--button-bg-color);
    transition: all .3s ease-in-out;

    @include hover {
      border: 1px solid var(--subaccent-color);
    }

    &:focus {
      border: 1px solid var(--subaccent-color);
    }

    @media (width <=480px) {
      width: 100%;
    }
  }

  &__error {
    position: absolute;
    top: 7px;
    font-size: 10px;
    color: var(--alert-color);
    opacity: 0;
    transition: all .3s ease-in-out;
  }
}

